<template>
   <div class="chart-wrpper">
    <canvas ref="myCanvas" class="canvas" width="140" height="140"></canvas>
  </div>
</template>
<script>
export default {
  props: {
    fillColor: String,
    txt: {
      type: String,
      default: 'hello world'
    },
    ratio: {
      type: Number,
      default: 0
    }
  },
  watch: {
    txt () {
      this.writeTxt()
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.writeTxt()
    })
  },
  methods: {
    writeTxt () {
      const canvas = this.$refs.myCanvas
      const ctx = canvas.getContext('2d')
      ctx.fillStyle = this.fillColor
      ctx.beginPath()
      ctx.arc(70, 70, 60, 0, Math.PI * 2)
      ctx.fill()
      ctx.beginPath()
      ctx.fillStyle = '#fff'
      ctx.font = '20px Arial'
      ctx.fillText(this.txt, 70 - ctx.measureText(this.txt).width / 2, 70)
      ctx.closePath()
      if (this.ratio !== 0) {
        ctx.beginPath()
        ctx.fillText(this.ratio * 100 + '%', 70 - ctx.measureText(this.ratio).width / 2, 100)
      }
    }
  }
}
</script>
<style lang="scss">
  .chart-wrpper {
    display: flex;
    justify-content: center;
    align-items: center;
    .canvas {
      position: absolute;
      top: 0; right: 0; bottom: 0; left: 0;
      margin: auto;
    }
  }
</style>



