<template>
   <div class="chart-wrpper">
    <canvas ref="myCanvas" :width="width" :height="height" class="canvas"></canvas>
  </div>
</template>
<script>
export default {
  props: {
    width: Number,
    height: Number,
    radius: Number,
    startPoint: String,
    strokeColor: String,
    lineWidth: Number,
    ratio: Number
  },
  mounted () {
    this.$nextTick(() => {
      let startPoint = Math.floor(Math.random() * 19)
      const canvas = this.$refs.myCanvas
      const ctx = canvas.getContext('2d')
      ctx.lineWidth = this.lineWidth
      ctx.strokeStyle = this.strokeColor
      ctx.beginPath()
      ctx.arc(this.height / 2, this.width / 2, this.radius, Math.PI / startPoint, 2 * Math.PI * this.ratio + Math.PI / startPoint)
      ctx.stroke()
    })
  }
}
</script>
<style lang="scss">
  .chart-wrpper {
    display: flex;
    justify-content: center;
    align-items: center;
    .canvas {
      position: absolute;
      top: 0; right: 0; bottom: 0; left: 0;
      margin: auto;
    }
  }
</style>



