<template>
  <div class="four">
    <div class="four-wrapper">
      <div class="header">
        <h5 class="text">
          我的经历
        </h5>
        <div class="circular">
          <div class="mark">
            <h3 class="time">
            2018
            </h3>
            <h5>YEAR</h5>
          </div>
        </div>
      </div>
      <div class="content">
        <div
          class="experience"
          v-for="(experience, indx) in experiences"
          :key="indx">
          <div class="time">
            <span class="year">{{experience.year}} </span>
            <span class="month">{{experience.month}} </span>
          </div>
          <div class="pointer-wrapper">
            <span class="ponint"></span>
          </div>
          <div class="content">
            <h3 class="title">
              {{experience.content.title}}
            </h3>
            <h5 class="subtitle">
              {{experience.content.subtitle}}
            </h5>
            <p class="text">
              {{experience.content.text}}
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      experiences: [
        {
          year: 2018,
          month: '03月',
          content: {
            title: '武夷山维芯网络有限公司',
            subtitle: '<技术部/前端H5工程师>',
            text: '负责PC端移动端页面制作。PSD切图后构建页面框架利用HTML、JS、CSS3书写前端功能，让页面更充实丰富。2018年06月初转正。'
          }
        },
        {
          year: 2019,
          month: '09月',
          content: {
            title: '武夷山维芯网络有限公司',
            subtitle: '<技术部/前端H5工程师>',
            text: '负责PC端移动端页面制作，小程序前端页面编写，维护。09月辞职。'
          }
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  $bgColor: rgb(215, 216, 216);
  .four {
    position: relative;
    width: 100%;
    height: 100vh;
    background: #000 url('../../assets/img/f_bg1.jpg') no-repeat center/cover;
    .four-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 100px;
      // justify-content: center;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.3);
      .header {
        display: flex;
        align-items: center;
        width: 70%;
        .text {
          flex: 0 0 100px;
          width: 100px;
          text-align: left;
          color: rgb(255, 210, 0);;
          font-size: 18px;
        }
        .circular {
          position: relative;
          display: flex;
          flex: 0 0 93px;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          border: 6px solid $bgColor;
          border-radius: 50%;
          width: 87px;
          height: 87px;
          .mark {
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            transition: all .5s linear;
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 50%;
          }
          .mark:hover {
            border: 6px solid rgba(107, 195, 13, 0.623);
            transform: rotate(360deg);
            border-radius: 50%;
            background: rgba(107, 195, 13, 0.623) url('../../assets/img/shan.png') no-repeat center;
            background-size: 100px 100px;
            transition: transform .5s linear;
            .time, h5 {
              font-size: 0px;
            }
          }
          .time {
            font-size: 25px;
            font-weight: bold;
            font-family: 微软雅黑;
            text-align: center;
            color: rgb(107, 195, 13)
          }
          h5 {
            font-size: 15px;
            color: rgb(144, 175, 112);
            font-weight: bold;
          }
        }
        .circular::after {
          content: '';
          position: absolute;
          margin-top: 6px;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          width: 10px;
          height: 60vh;
          background: $bgColor
        }
      }
      .content {
        max-width: 70%;
        .experience:hover {
          .content {
            .title {
              color: rgb(107, 195, 13);
            }
          }
          .pointer-wrapper {
            .ponint {
              transition: all .3s linear;
              border: 8px solid rgba(107, 195, 13, .7);
            }
          }
        }
        .experience {
          display: flex;
          align-items: center;
          cursor: pointer;
          .time {
            width: 100px;
            text-align: left;
            flex-shrink: 0;
            .year {
              margin-right: 8px;
              color: rgb(247, 232, 73);
              font-size: 12px;
            }
            .month {
              color: rgb(247, 232, 73);
              font-size: 18px;
            }
          }
          .pointer-wrapper {
            display: flex;
            flex-shrink: 0;
            justify-content: center;
            width: 103px;
            .ponint {
              position: relative;
              display: inline-block;
              width: 20px;
              height: 20px;
              border-radius: 50%;
              background: $bgColor;
              border: 3px solid greenyellow;
              transition: all .3s linear;
            }
            .ponint::after {
              content: '';
              position: absolute;
              width: 25px;
              height: 3px;
              background: greenyellow;
              right: 100%;
              top: 50%;
              transform: translateY(-50%);
            }
          }
          .content {
            flex-shrink: 1;
            margin-top: 30px;
            .title {
              font-weight: 100;
              font-size: 24px;
              color: #fff;
              line-height: 24px;
            }
            .subtitle {
              font-size: 13px;
              line-height: 30px;
              color: rgb(140, 168, 112);
            }
            .text {
              font-size: 14px;
              color: #fff;
            }
          }
        }
      }
    }
  }
</style>



