<template>
  <div ref="home" class="home">
    <div ref="contentWrapper" class="content-wrapper">
      <l-first />
      <l-second />
      <l-third />
      <l-four />
      <l-five />
    </div>
    <div class="common-fixed">
      <div class="navs-page-wrapper" v-show="showNavsPage">
      </div>
      <l-navs-page
       :curPage="pageMode"
       :navList="navList"
       :showNavsPage="showNavsPage"
       @onClick="togglePageModeAndHideNavsPage"
      />
     <div class="del-btn" @click="toggleShowNavsPage">
       <div class="heng-wrapper">
        <i ref="fHeng" class="heng"></i>
        <i ref="sHeng" class="heng"></i>
        <i ref="tHeng" class="heng"></i>
       </div>
     </div>
     <div class="navs-wrapper">
        <l-navs
         :curIndex="pageMode"
         @onClick="togglePageMode" />
        <div class="bgm"></div>
     </div>
    </div>
  </div>
</template>

<script>
import lFirst from '@/components/first'
import lSecond from '@/components/second'
import lThird from '@/components/third'
import lFour from '@/components/four'
import lFive from '@/components/five'
import lNavs from '@/components/base/l-navs'
import lNavsPage from '@/components/base/l-navs-page'
export default {
  components: {
    lFirst,
    lSecond,
    lThird,
    lFour,
    lFive,
    lNavs,
    lNavsPage
  },
  data () {
    return {
      windowHeight: 0,
      showSecond: false,
      pageMode: 0,
      showNavsPage: false,
      navList: [
        '首页',
        '关于我们',
        '技能掌握',
        '我的经历',
        '我的作品'
      ]
    }
  },
  created () {
    this.hideScrollYBar()
  },
  mounted () {
    this.initMove()
    window.addEventListener('resize', () => {
      this.windowHeight = document.body.offsetHeight - document.body.scrollTop
      this.$refs.contentWrapper.style.transform = `translateY(-${this.pageMode * this.windowHeight}px)`
    })
  },
  watch: {
    pageMode (newPageMode) {
      this.$refs.contentWrapper.style.transform = `translateY(-${newPageMode * this.windowHeight}px)`
      this.$refs.contentWrapper.style.transition = 'all .5s linear'
    }
  },
  methods: {
    hideScrollYBar () {
      document.body.parentNode.style.overflowY = 'hidden'
    },
    initMove () {
      this.$nextTick(() => {
        this.windowHeight = document.body.offsetHeight - document.body.scrollTop
        this.lock = false
        document.addEventListener('mousewheel', (e) => {
          this.moveHandler(e)
        })
      })
    },
    togglePageMode (pageMode) {
      this.pageMode = pageMode
    },
    toggleShowNavsPage () {
      this.showNavsPage = !this.showNavsPage
      if (this.showNavsPage) {
        this.hengTransition(this.$refs.fHeng, 'transform', 'rotate(45deg)')
        this.hengTransition(this.$refs.fHeng, 'width', '43px')
        this.hengTransition(this.$refs.fHeng, 'transition', 'all .5s')
        this.hengTransition(this.$refs.sHeng, 'transform', 'translateX(-100%)')
        this.hengTransition(this.$refs.sHeng, 'transition', 'all .5s')
        this.hengTransition(this.$refs.tHeng, 'transform', 'rotate(-45deg)')
        this.hengTransition(this.$refs.tHeng, 'width', '43px')
        this.hengTransition(this.$refs.tHeng, 'transition', 'all .5s')
      } else {
        this.hengTransition(this.$refs.fHeng, 'transform', 'rotate(0deg)')
        this.hengTransition(this.$refs.fHeng, 'width', '30px')
        this.hengTransition(this.$refs.fHeng, 'transition', 'all .5s')
        this.hengTransition(this.$refs.sHeng, 'transform', 'translateX(0)')
        this.hengTransition(this.$refs.sHeng, 'transition', 'all .5s')
        this.hengTransition(this.$refs.tHeng, 'transform', 'rotate(0deg)')
        this.hengTransition(this.$refs.tHeng, 'width', '30px')
        this.hengTransition(this.$refs.tHeng, 'transition', 'all .5s')
      }
    },
    togglePageModeAndHideNavsPage (curPage) {
      this.togglePageMode(curPage)
      this.toggleShowNavsPage()
      this.showNavsPage = false
    },
    hengTransition (el, porperty, name) {
      el.style[porperty] = name
    },
    moveHandler (e) {
      if (this.lock) {
        return
      }
      this.lock = true
      setTimeout(() => {
        this.lock = false
      }, 500)
      let pageMode = this.pageMode
      if (e.wheelDeltaY < 0) {
        if (pageMode >= 4) return
        pageMode++
      } else {
        if (pageMode <= 0) return
        pageMode--
      }
      this.pageMode = pageMode
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss"  scoped>
  $blue: rgb(51, 122, 183);
  .home {
    width: 100%; height: 100vh;
    overflow: hidden;
  }
  .content-wrapper {
    background: transparent;
  }
  .move-enter-active, .move-leave-active {
    transform: translateY(-600px);
    transition: all .5s;
  }
  .move-enter, .move-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
  .hide-enter-active, .hide-leave-active {
    transform: translateY(-600px);
    transition: all .5s;
  }
  .hide-enter, .hide-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
  .common-fixed {
    position: fixed;
    top: 0; left: 0;
    z-index: 1000;
    .del-btn {
      position: fixed;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      top: 20px;
      right: 20px;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background: transparent;
      box-shadow: 1px 1px 1px 2px rgba(0, 0, 0, 0.63);
      transition: all .5s;
      cursor: pointer;
      .heng-wrapper {
        width: 30px;
        overflow: hidden;
        .heng {
          display: block;
          transform-origin: 0;
          margin-bottom: 7.5px;
          width: 30px;
          height: 5px;
          border-radius: 2.5px;
          background: #fff;
        }
        .heng:last-child {
          margin-bottom: 0px;
        }
      }
    }
    .del-btn:hover {
      background: $blue;
      transition: all .5s;
    }
    .del-btn:hover::after {
      content: '导航';
      position: absolute;
      left: 50%;
      top: 100%;
      width: 60px;
      height: 30px;
      line-height: 30px;
      background: $blue;
      border-radius: 10px;
      text-align: center;
      color: #fff;
      transform: translateX(-50%);
    }
    .navs-wrapper {
      position: fixed;
      z-index: -100;
      display: flex;
      align-items: center;
      flex-direction: column;
      right: 40px;
      top: 50%;
      transform: translateY(-50%);
      .bgm {
        width: 50px;
        height: 50px;
        background: red url('../assets/img/first_bg1.jpg') no-repeat center/cover;
        border-radius: 50%;
        transform: rotate(360deg);
        animation: bgm 15s linear infinite;
      }
    }
  }
  .navs-page-wrapper {
    position: fixed;
    top: 0; left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.63);
  }
  @keyframes bgm {
    form {
      transform: rotate(360deg);
    }
    to {
      transform: rotate(0deg);
    }
  }
</style>



