<template>
  <transition name="navs-page">
    <div class="nvas-page" v-if="showNavsPage">
      <div class="nav-list">
        <div
         class="nav"
         @click="toggleCurPage(indx)"
         :key="indx"
         v-for="(nav, indx) in navList">
          <span
           class="text"
           :class="{active: curPage === indx}"
          >{{nav}}</span>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    showNavsPage: Boolean,
    curPage: {
      type: Number,
      default: 0
    },
    navList: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    toggleCurPage (indx) {
      this.$emit('onClick', indx)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .nvas-page {
    position: fixed;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    top: 0;
    right: 0;
    width: 30%;
    height: 100%;
    background: transparent url('../../../assets/img/nav_page_bg.png') no-repeat;
    background-size: 100% 100%;
    .nav-list {
      display: flex;
      height: 60%;
      flex-direction: column;
      justify-content: space-around;
      align-items: flex-end;
      padding-right: 20px;
      .nav {
        .text {
          position: relative;
          cursor: pointer;
          text-align: right;
          padding: 20px;
          font-size: 25px;
        }
        .active::after, .text:hover::after {
          content: '';
          position: absolute;
          right: 0px;
          top: 50%;
          transform: translateY(-50%);
          width: 100%;
          height: 2px;
          background: blue;
          transition: all 1s;
        }
        .active {
          text-decoration: line-through;
        }
      }
    }
  }
  .navs-page-enter-active, .navs-page-leave-active {
    transition: all .5s;
  }
  .navs-page-enter, .navs-page-leave-to {
    transform: translateX(100%);
    opacity: 0;
  }
</style>



