<template>
  <div class="five">
    <div class="five-wrapper">
      <div v-for="(web, indx) in filterWebList" :key="indx" class="web-wrapper">
        <l-web
          ref="web"
          :key="web.name"
          :href="web.href"
          :qrUrl="web.qrUrl"
          :bgImgSrc="web.bgImgSrc"
          :name="web.name"/>
      </div>
    </div>
    <div class="page-list">
      <div
        class="page-item"
        :key="indx"
        :class="{active: curIndx === indx}"
        @click="toggleCurIndx(indx)"
        v-for="(web, indx) in webList">
        {{indx + 1}}
      </div>
    </div>
  </div>
</template>

<script>
import lWeb from '@/components/five/l-web'
export default {
  components: {
    lWeb
  },
  data () {
    return {
      curIndx: 0,
      webList: [
        {
          name: '点击访问 仿美团',
          bgImgSrc: require('../../assets/img/meituan.jpg'),
          href: 'http://smallfat.top/meituan'
        },
        {
          name: '小程序: 音乐播放器',
          bgImgSrc: require('../../assets/img/xcx_music.jpg'),
          qrUrl: require('../../assets/img/qr2.jpg')
        },
        {
          name: 'eagles troop',
          bgImgSrc: require('../../assets/img/eagle.jpg'),
          href: 'http://smallfat.top/boke'
        },
        {
          name: '移动端 高仿饿了吗',
          bgImgSrc: require('../../assets/img/elema.jpg'),
          href: 'http://smallfat.top/elema'
        }
      ],
      filterWebList: [
        {
          name: '点击访问 仿美团',
          bgImgSrc: require('../../assets/img/meituan.jpg'),
          href: 'http://smallfat.top/meituan'
        },
        {
          name: '小程序: 音乐播放器',
          bgImgSrc: require('../../assets/img/xcx_music.jpg'),
          qrUrl: require('../../assets/img/qr2.jpg')
        }
      ]
    }
  },
  watch: {
    curIndx (newIndx) {
      newIndx = Math.min(newIndx, this.webList.length - 2)
      this.filterWebList = this.webList.slice(newIndx, newIndx + 2)
    }
  },
  methods: {
    toggleCurIndx (indx) {
      this.curIndx = indx
      this.$refs.web.map(web => {
        web.hideQr()
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .five {
    position: relative;
    width: 100%;
    height: 100vh;
    background: #000 url('../../assets/img/fi_bg1.jpg') no-repeat center/cover;
    .five-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.3);
      .web-wrapper {
        width: 400px;
        height: 400px;
        padding: 20px;
      }
    }
    .page-list {
      position: absolute;
      justify-content: center;
      display: flex;
      width: 100%;
      bottom: 10%; right: 0; left: 0;
      margin: 0 auto;
      .page-item {
        text-align: center;
        height: 30px;
        line-height: 30px;
        margin-right: 20px;
        width: 30px;
        color: #fff;
        font-size: 25px;
        background: #666;
        cursor: pointer;
      }
      .page-item.active {
        background: greenyellow;
      }
    }
  }
</style>



