<template>
  <div class="skill-wrapper">
    <div
      class="language"
      :style="{background: bgColor, width: ratio * 100 + '%'}">
      {{txt}}
    </div>
  </div>
</template>

<script>
export default {
  props: {
    bgColor: String,
    txt: String,
    ratio: Number
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .skill-wrapper {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    height: 35px;
    .language {
      cursor: pointer;
      color: #fff;
      text-align: center;
      line-height: 35px;
      font-size: 18px;
      border-bottom: 1px solid #555;
    }
  }
</style>



