<template>
  <div class="second">
    <div class="second-wrapper">
      <div class="avatar">
        <img class="pic" :src="picSrc" />
      </div>
      <div class="text">
        <p>
        <strong>-幸会，我是Smallfat！</strong>
        <br />一名前端工程师。打造一款成功的产品离不开持之以恒的照料，能成为你的搭档创造出别具一格的作品，我倍感荣幸。
        <br />
        <strong>-我能效劳</strong>
        <br />前端开发，MVVM模块化开发，利用各种技巧实现交互丰富的界面。
        <br />
        <strong>-关于我</strong>
        <br />曾就职于武夷山微信网络有限公司，主要从事小程序购物商城前端开发。技术栈vue+mpvue+es6+jquery+elema ui
        <br />不浮躁，执行力，学习能力都还不错！目前，能做点粗糙ps设计。
      </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      picSrc: require('@/assets/img/s_avatar.jpg')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss"  scoped>
.second {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #000 url('../../assets/img/s_bg1.jpg') no-repeat center/cover;
  .second-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    background: rgba(255, 255, 255, 0.3);
    .avatar {
      width: 200px;
      height: 200px;
      border-radius: 50%;
      overflow: hidden;
      .pic {
        height: 100%;
      }
    }
    .text {
      font-family: 微软雅黑, Verdana, Arial, SimSun;
      margin-top: 25px;
      max-width: 60%;
      font-size: 1rem;
      line-height: 30px;
      text-align: justify;
      color: #fff;
    }
  }
}
</style>



