<template>
  <div class='third'>
    <div class='third-wrapper'>
      <div class="skill">
        <div class="legend">
          Skills
        </div>
        <div
          @mouseenter="enhanceShow(indx)"
          @mouseleave="enhanceShow(100)"
          class="skill-item" v-for="(chart, indx) in charts" :key="chart.skill">
          <l-skill
           :bgColor="chart.strokeColor"
           :txt="chart.skill"
           :ratio="chart.ratio"
          />
        </div>
      </div>
      <div class="chart-wrpper">
        <div style="width: 140px; height: 140px;" class="chart none">
          <l-react
            fillColor='rgb(25, 51, 64)'
            :txt='skill'
            :ratio="ratio"
          />
        </div>
        <div
        @mouseleave="enhanceShow(100)"
        @mouseenter="enhanceShow(indx)"
        :style="{zIndex: charts.length - indx, width: (chart.radius * 2 + chart.lineWidth) + 'px', height: (chart.radius * 2 + chart.lineWidth) + 'px'}"
        v-for="(chart, indx) in charts"
        :key="indx" class="chart">
        <!-- <path M 200,200 H 300></path> -->
        <l-chart
          :width="chart.radius * 2 + chart.lineWidth"
          :height="chart.radius * 2 + chart.lineWidth"
          :radius="chart.radius"
          :strokeColor= 'chart.strokeColor'
          :lineWidth='chart.lineWidth'
          :ratio='chart.ratio'
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import lChart from '@/components/third/l-chart'
import lReact from '@/components/third/l-react'
import lSkill from '@/components/third/l-skill'
export default {
  components: {
    lChart,
    lReact,
    lSkill
  },
  data () {
    return {
      skill: 'Skill',
      ratio: 0,
      charts: [
        {
          skill: 'Css',
          ratio: 0.8,
          radius: 70,
          lineWidth: 15,
          strokeColor: 'rgba(228, 167, 137, 1)'
        },
        {
          skill: 'Html5',
          ratio: 0.8,
          radius: 90,
          lineWidth: 15,
          strokeColor: '#' + Math.random().toString(15).substring(2, 8)
        },
        {
          skill: 'Javascript',
          ratio: 0.8,
          radius: 110,
          lineWidth: 15,
          strokeColor: '#' + Math.random().toString(15).substring(2, 8)
        },
        {
          skill: 'Typescript',
          ratio: 0.6,
          radius: 130,
          lineWidth: 15,
          strokeColor: '#' + Math.random().toString(15).substring(2, 8)
        },
        {
          skill: 'Jquery',
          ratio: 0.7,
          radius: 150,
          lineWidth: 15,
          strokeColor: '#' + Math.random().toString(15).substring(2, 8)
        },
        {
          skill: 'Vue',
          ratio: 0.8,
          radius: 170,
          lineWidth: 15,
          strokeColor: '#' + Math.random().toString(15).substring(2, 8)
        },
        {
          skill: 'Es6',
          ratio: 0.8,
          radius: 190,
          lineWidth: 15,
          strokeColor: '#' + Math.random().toString(15).substring(2, 8)
        },
        {
          skill: 'react',
          ratio: 0.6,
          radius: 210,
          lineWidth: 15,
          strokeColor: '#' + Math.random().toString(15).substring(2, 8)
        },
        {
          skill: 'Mpvue',
          ratio: 0.6,
          radius: 230,
          lineWidth: 15,
          strokeColor: '#' + Math.random().toString(15).substring(2, 8)
        }
      ]
    }
  },
  methods: {
    enhanceShow (indx) {
      this.skill = this.charts[indx] ? this.charts[indx].skill : 'Skill'
      this.ratio = this.charts[indx] ? this.charts[indx].ratio : 0
    }
  }
}
</script>
<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.third {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #000 url('../../assets/img/t_bg1.jpg') no-repeat center/cover;
  .third-wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.3);
    // background: url('../../assets/img/third_bg.jpg') no-repeat center/cover;
    .skill {
      display: flex;
      flex-direction: column;
      width: 50%;
      justify-content: center;
      align-items: center;
      .legend {
        font-family: "Cabin Sketch", arial, serif;
        text-shadow: rgb(221, 221, 221) 3px 3px 0px;
        color: rgb(255, 210, 0);
        font-size: 40px;
        margin-bottom: 25px;
      }
      .skill-item {
        width: 200px;
      }
    }
    .chart-wrpper {
     display: flex;
     justify-content: center;
     align-items: center;
     width: 50%;
     height: 100%;
     position: relative;
     .chart {
       position: absolute;
       top: 0; right: 0; bottom: 0; left: 0;
       margin: auto;
       border-radius: 50%;
       overflow: hidden;
     }
     .none {
       z-index: 1000;
       background: '';
     }
    }
  }
}
</style>



