<template>
  <div
   class="instroduce"
   ref="instroduce"
   @mousemove="onMousemove"
   @mouseleave="onMouseleave"
   :class="{shadow: showShadow}">
  <h2 class="word-no-wrap">Hello, I'm smallFat</h2>
  <strong class="word-no-wrap">Life doesn’t get easier, you just get stronger</strong>
  <p class="W-p1">我叫smallFat</p>
  <p class="W-p2">一名前端开发工程师</p>
  <p class="W-mailbox">541953126@qq.com</p>
</div>
</template>

<script>
export default {
  props: {
    showShadow: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      showMove: false
    }
  },
  mounted () {
    this.instroduceEle = this.$refs.instroduce
    this.centerPoint = {
      x: document.body.offsetWidth / 2,
      y: (document.body.offsetHeight - document.body.scrollTop) / 2
    }
  },
  methods: {
    onMousemove (e) {
      const dirX = this.centerPoint.x - e.pageX
      const dirY = this.centerPoint.y - e.pageY
      this.instroduceEle.style.transform = `translateY(-45%) skew(${dirX / 10}deg, ${dirY / 10}deg)`
    },
    onMouseleave () {
      this.instroduceEle.style.transform = 'translateY(-50%)'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .shadow {
    box-shadow: 0px 0px 40px 3px  rgba(0, 0, 0, 0.267);
  }
  .instroduce {
    cursor: pointer;
    position: absolute;
    border-radius: 15px;
    padding: 30px;
    color: #000;
    top: 50%; right: 0; left: 0;
    transform: translateY(-50%);
    margin:0 auto;
    text-align: center;
    width: 40%;
    transition: all .5s linear;
    h2 {
      font-size: 50px;
      font-weight: 700;
    }
    strong {
      display: block;
      font-size: 25px;
    }
    .W-p1, .W-p2, .W-mailbox {
      font-size: 20px;
      line-height: 40px;
      color: #000;
    }
    .W-mailbox {
      color: #7d2704;
    }
  }
  .instroduce:hover {
    box-shadow: 0px 0px 50px 10px rgba(0, 0, 0, 0.356);
    transition: all .5s linear;
  //   transform: translateY(-45%) skew(-5deg, -5deg);
  }
</style>



