<template>
  <div
    class="first"
    ref="first"
    @mouseenter="toggleShadow(true)"
    @mouseleave="toggleShadow(false)">
      <div class="first-wrapper">
        <l-instroduce
        :showShadow="showShadow"
        />
      </div>
  </div>
</template>

<script>
import lInstroduce from '@/components/first/l-instroduce'
import bgUrl0 from '../../assets/img/first_bg1.jpg'
import bgUrl1 from '../../assets/img/first_bg2.jpg'
const INTERVAL = 10000
export default {
  components: {
    lInstroduce
  },
  data () {
    return {
      showShadow: false,
      bgUrl1: bgUrl1,
      bgUrl0: bgUrl0
    }
  },
  mounted () {
    this.imgIndx = 0
    window.setInterval(() => {
      this.imgIndx++
      let bgUrl = this.imgIndx % 2 ? this.bgUrl1 : this.bgUrl0
      this.$refs.first.style.background = `url(${bgUrl}) no-repeat center/cover`
    }, INTERVAL)
  },
  methods: {
    toggleShadow (bool) {
      this.showShadow = bool
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .first {
    position: relative;
    width: 100%;
    height: 100vh;
    background: url('../../assets/img/first_bg1.jpg') no-repeat center/cover;
    overflow: hidden;
    .first-wrapper {
      width: 100%;
      height: 100vh;
      background: rgba(255, 255, 255, .3);
    }
  }
</style>



