<template>
  <div class="navs">
    <div
     class="nav"
     :class="{active: curIndex === indx}"
     :key="indx"
     @click="toggleCurIndex(indx)"
     v-for="(nav, indx) in navs">
    </div>
  </div>
</template>
<script>
export default {
  props: {
    navs: {
      type: Array,
      default: () => new Array(5).fill(1)
    },
    curIndex: {
      type: Number,
      default: 0
    }
  },
  methods: {
    toggleCurIndex (indx) {
      this.$emit('onClick', indx)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .navs {
    .nav {
      position: relative;
      margin-bottom: 25px;
      width: 8px;
      height: 8px;
      border: 3px solid #fff;
      border-radius: 50%;
      background: blue;
      cursor: pointer;
    }
    .nav::after {
      content: '';
      position: absolute;
      top: 100%; left: 50%;
      transform: translateX(-50%);
      margin-top: 3px;
      height: 25px;
      border-right: 1px dashed #222;
    }
    .active {
      animation: light 1s linear infinite;
    }
  }
  @keyframes light {
    form {
      box-shadow: 0 0 0px 0px blue;
    }
    to {
      box-shadow: 0 0 1px 10px rgba(0, 0, 255, 0.233);
    }
  }
</style>



