<template>
  <a
    ref="aEle"
    @mouseenter="onMouseenter"
    @mouseleave="onMouseleave"
   :href="href" target="_Blank" class="web"
   :style="{background: '#fff url('+bgImgSrc+') no-repeat center/contain'}"
   @click="showQr"
   >
    <div class="web-name word-no-wrap">
      {{name}}
    </div>
    <div class="pic-wrapper">
      <img ref="pic" class="pic" :src="bgImgSrc" />
    </div>
    <transition name="fade">
      <div class="qr-wrapper" v-if="showQrStatus">
        <img class="qr" :src="qrUrl" />
      </div>
    </transition>
  </a>
</template>

<script>
export default {
  props: {
    href: {
      type: String,
      default: 'javascript:;'
    },
    bgImgSrc: String,
    name: String,
    qrUrl: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      showQrStatus: false
    }
  },
  methods: {
    onMouseenter () {
      const picEle = this.$refs.pic
      const aEleHeight = this.$refs.aEle.offsetHeight
      const height = picEle.offsetHeight - aEleHeight
      const timeSecond = height / 100
      picEle.style.transform = `translateY(-${height}px)`
      picEle.style.transition = `all ${timeSecond}s linear `
    },
    onMouseleave () {
      const picEle = this.$refs.pic
      const aEleHeight = this.$refs.aEle.offsetHeight
      const height = picEle.offsetHeight - aEleHeight
      const timeSecond = height / 100
      picEle.style.transform = 'translateY(0px)'
      picEle.style.transition = `all ${timeSecond}s linear`
    },
    showQr () {
      if (this.qrUrl) {
        this.showQrStatus = true
      }
    },
    hideQr () {
      this.showQrStatus = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .web {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    opacity: .5;
    cursor: pointer;
    transition: all .5s linear;
    .pic-wrapper {
      display: flex;
      width: 100%;
      position: absolute;
      top: 0px;
      justify-content: center;
      align-items: center;
      .pic {
        width: 100%;
      }
    }
    .web-name {
      position: absolute;
      z-index: 1;
      padding: 0 20px;
      color: #fff;
      font-size: 25px;
      font-weight: 500;
      background: #666;
      transition: all .5s linear;
    }
    .qr-wrapper {
      position: absolute;
      z-index: 10;
      left: 0; top: 8%; bottom: 0; right: 0;
      margin: auto;
      width: 80px;
      height: 80px;
      padding: 10px;
      margin: 0 auto;
      // transition: all 1s linear;
      background: #fff;
      .qr {
        width: 100%;
        height: 100%;
      }
    }
  }
  .web:hover {
    transition: all .5s linear;
    opacity: 1;
    transform: scale(1.1);
    .web-name {
      transform: translateY(5px);
      transition: all .5s linear;
    }
  }
  a {
    text-decoration: none;
  }
  .fade-enter-active{
    // transform: translateY(-50%);
    transition: all .8s linear;
  }
  .fade-enter /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateY(100%) translateX(200px);
  }
</style>



